* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

body {
  background: #f7f7f7;
  font-size: 14px;
  font-family: 'microsoft yahei';
}

body:before {
  content: '';
  position: fixed;
  top: -50px;
  left: 0;
  width: 100%;
  height: 50px;
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, .3);
          box-shadow: 0 0 15px rgba(0, 0, 0, .3);
  z-index: 2019;
}

h1 {
  height: 0;
  line-height: 0;
  width: 0;
  text-indent: -99999px;
}

.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}

#app {
  padding: 70px 0;
  text-align: center;
}

.vertical-center {
  vertical-align: middle;
}


