.cell {
  text-align: center;
  display: grid;
  grid-template: 15px 1fr 15px / 10px 1fr 10px;
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
}

.cell:hover {
  -webkit-animation: 2.5s 2s rock infinite;
          animation: 2.5s 2s rock infinite;
}

.cell:hover a {
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}

.cell a {
  position: relative;
  display: grid;
  grid-area: 2 / 2;
  grid-template: 70px 28px / 100%;
  border-radius: 6px;
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.cell a .corner {
  position: absolute;
  top: 0;
  right: 0;
  width: 0px;
  height: 0px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  border-radius: 0 6px 0 6px;
  background: -webkit-linear-gradient(45deg, #eee 40%, #9E9E9E 50%, #2f3242 56%);
  background: -o-linear-gradient(45deg, #eee 40%, #9E9E9E 50%, #2f3242 56%);
  background: linear-gradient(45deg, #eee 40%, #9E9E9E 50%, #2f3242 56%);
}

.cell a:hover {
  -webkit-animation: none;
          animation: none;
  -webkit-transition: 0s;
  -o-transition: 0s;
  transition: 0s;
  -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, .08);
          box-shadow: 0 0 30px rgba(0, 0, 0, .08);
  background: #fff;
}

.cell a:hover .corner {
  width: 22px;
  height: 22px;
  -webkit-transition: 0.2s .5s;
  -o-transition: 0.2s .5s;
  transition: 0.2s .5s;
}

.cell a .corner:hover {
  width: 30px;
  height: 30px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.cell .img-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.cell img {
  width: 50px;
  border-radius: 4px;
}

.cell .title {
  font-size: 14px;
  color: #757575;
}

@-webkit-keyframes rock {
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  80% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }

  85% {
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }

  90% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
  }

  95% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
}

@keyframes rock {
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  80% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }

  85% {
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
  }

  90% {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
  }

  95% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
}


